}
},
err => {
alert("网络缓慢。。");
}
);
},





addAddress(item) {
console.log(item);
if (item != 'ent') {
this.pathF = true;
}
this.$router.push({
path: "/addressopt",
query: {
obj: JSON.stringify({
type: "profession",
data: {
item,
pathF: this.pathF
}
})
}
});

},

getAddress() {
this.$fetch
.post("fruits/app/cart/getAddressList", { openId: url.openId })
.then(res => {
this.addressList = [...res.obj];
});
}
},
created() {
settitle("地址管理");
},
mounted() {
this.getAddress();
}
};



